@import "./library/bootstrap";
@import "./main";

html,
body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif !important;
  box-sizing: border-box;
  text-decoration: unset !important;
  list-style: unset !important;
}


.fw-500 {
  font-weight: 500;
}

.fw-600 {
  font-weight: 600;
}

.fw-800 {
  font-weight: 800;
}

.rounded-16 {
  border-radius: 16px;
}

.rounded-8 {
  border-radius: 8px;
}

.text-three-line {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.focus-none:focus {
  outline: unset !important;
  box-shadow: unset !important;
}

.font-DM {
  font-family: 'DM Sans', sans-serif;
}

.box {
  background: #FFFFFF;
  box-shadow: 0 10px 20px rgba(18, 38, 63, 0.0313726);
  border-radius: 3px;
  padding: 20px 24px;
}

.content {
  background: #FFFFFF;
  box-shadow: 0 8px 15px rgba(149, 156, 182, 0.15);
  border-radius: 4px;

  &-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 18px 16px;
    border-bottom: 1px solid #D1D5DB;
    border-radius: 4px 4px 0 0;

    &_title {
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: #84818A;
      margin-bottom: 0;
    }
  }

  &-body {
    padding: 18px 16px;
    border-radius: 0 0 4px 4px;
  }

}


.btn-back {
  background: #FFFFFF;
  box-shadow: 0px 10px 20px rgba(18, 38, 63, 0.0313726);
  border-radius: 4px;
}


